import React from 'react';
import './assets/navbar.css';
import { Navbar, Button, Form, TabContainer, Row } from 'react-bootstrap';
import { Link, useHistory } from 'react-router-dom';
import NavItems from './Nav';
import { Popover } from 'antd';
const Header = () => {
	const history = useHistory();
	const content = (
		<div>
			<Row>
				<Link to="/signup" className="text-center w-100 h5">
					Sign up
				</Link>
			</Row>
			<Row>
				<Link to="/login" className="text-center w-100 h5">
					Sign in
				</Link>
			</Row>
		</div>
	);
	return (
		<Navbar
			bg="light"
			expand="lg"
			sticky="top"
			className="nav menu-desktop  fixed animated slideInDown"
		>
			<TabContainer>
				<Navbar.Brand>
					<Link to="/home">LOGO</Link>
				</Navbar.Brand>
				<Navbar.Toggle aria-controls="navbarScroll" />
				<Navbar.Collapse id="navbarScroll">
					<NavItems />
					<Form className="d-flex justify-content-between">
						<Button
							className="shop mr-4"
							onClick={() => history.push('/shop')}
						>
							SHOP
						</Button>
						<Popover content={content} title="Title">
							<Button className="account">ACCOUNT</Button>
						</Popover>
					</Form>
				</Navbar.Collapse>
			</TabContainer>
		</Navbar>
	);
};

export default Header;
